@import '../../utils/mixins.scss';
// @import '../../utils/reset.scss';
@import '../../utils/variable.scss';

@mixin pageGrid {
  --columns: 1;
  display: grid;
  row-gap: 24px;
  column-width: 272px;
  grid-template-columns: repeat(var(--columns), 1fr);
  grid-column-gap: 16px;

  @include onTablet {
    --columns: 2;
    // max-width: 560px;
    // max-width: 100vw;
  }

  @include onDeskTop {
    --columns: 4;
    max-width: 1136px;
  }
}

.about {
  @include pageGrid;
  background-color: $HoverBg;
  width: 100%;
  // margin: 72px auto;
  padding: 72px;
  justify-self: center;
  line-height: 21px;
  // text-align: center;
}

.about-container {
  grid-column: span 1;

  @include onTablet() {
    grid-column: span 2;
  }

  @include onDeskTop() {
    grid-column: span 4;
  }
}

.tecnologies-container {
  grid-column: span 1;

  @include onTablet() {
    grid-column: span 1;
  }

  @include onDeskTop() {
    grid-column: span 2;
  }
}

.team-container {
  grid-column: span 1;

  @include onTablet() {
    grid-column: span 1;
  }

  @include onDeskTop() {
    grid-column: span 2;
  }
}

.page__title {
  grid-column: span 1;

  @include onTablet() {
    grid-column: span 2;
  }

  @include onDeskTop() {
    grid-column: span 4;
  }
}

.section-title {
  color: $Primary;
  size: 22px;
  font-weight: 500;

  &--secondary {
    size: 20px;
    font-weight: 400;
  }
}

.section__text {
  color: $Secondary;
}

.team__list {
  list-style: none;
  text-align: left;
  // padding-left: 124px;
}